
body {
  font-family: 'Asap', sans-serif;
  background-color: #e6ede9;
  font-size: 16px;
}
h1 {
  font-family: 'Belgrano', serif;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5), 1px 1px 1px rgba(0,0,0,0.5);
  font-size: 1.5em;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
label {
  font-size: 0.8em;
  display: block;
  margin-bottom: 0.5em;
}
.menu-container {
  position: fixed;
  bottom: 0;
  z-index: 50;
  margin: 5em;
}
.menu {
  border: #0d5948 1px solid;
  padding: 2em;
  background-color: #1aad8d;
  color: #fff;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.git-hub {
  margin-top: 1em;
}
.git-hub .octicon {
  padding: 0.1em;
  background-color: #fff;
  border-radius: 50%;
  display: inline-block;
}
.git-hub .octicon path {
  fill: #1aad8d;
}
.set {
  margin: 1em;
  display: inline-block;
}
.console {
  display: block;
  max-width: 20em;
  position: fixed;
  margin: 5em;
  right: 0;
  bottom: 0;
  color: #0d5948;
}
button,
select,
option {
  border: none;
  box-shadow: none;
  background-color: #fff;
  outline: 0;
}
button:focus,
select:focus,
option:focus {
  outline: 0;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appereance: none;
  box-sizing: border-box;
  line-height: 1.5em;
  padding: 0.5em 3.5em 0.5em 1em;
  color: #1aad8d;
  background-image: linear-gradient(45deg, transparent 50%, #1aad8d 50%), linear-gradient(135deg, #1aad8d 50%, transparent 50%), linear-gradient(to right, #ccc, #ccc);
  background-position: calc(100% - 20px) calc(1em + 2px), calc(100% - 15px) calc(1em + 2px), calc(100% - 2.5em) 0.5em;
  background-size: 5px 5px, 5px 5px, 1px 1.5em;
  background-repeat: no-repeat;
}
button.btn {
  padding: 0.5em 2em;
  font-size: 1em;
}
button.btn,
select {
  border: #179b7e solid 1px;
  box-shadow: 1px 1px 1px rgba(0,0,0,0.5);
  border-radius: 0.25em;
  color: #1aad8d;
  font-weight: bold;
  background-color: #fff;
}
#app {
  text-align: center;
}
.dialog-drag {
  min-width: 10em;
  background-color: #e6eee9;
  box-shadow: 2px 2px 1px rgba(0,0,0,0.5);
}
.dialog-1.dialog-drag {
  border: #1aad8d dashed 2px;
  background-color: #fff;
}
.dialog-1.dialog-drag .dialog-header {
  background-color: transparent;
}
.dialog-1.dialog-drag .dialog-header button.close {
  color: #1aad8d;
}
.dialog-1.dialog-drag .dialog-header .dialog-title {
  display: none;
}
.dialog-drag.dialog-1.fixed {
  border: #1aad8d solid 2px;
}
.dialog-2.dialog-drag {
  border-color: #1aad8d;
}
.dialog-2.dialog-drag .dialog-header {
  background-color: #1aad8d;
  color: #fff;
}
.dialog-2.dialog-drag .dialog-header button.close {
  color: #fff;
}
.dialog-3.dialog-drag {
  border: none;
  background-color: #fefaf1;
  -webkit-animation-name: dialog-3-anim;
          animation-name: dialog-3-anim;
}
.dialog-3.dialog-drag .dialog-header {
  background-color: #e59e0d;
  color: #fff;
}
.dialog-3.dialog-drag .dialog-header button.close {
  color: #fcedcf;
  text-shadow: none;
}
.dialog-3.dialog-drag .dialog-header button.close:hover {
  color: #fff;
  text-shadow: 1px 1px 1px rgba(0,0,0,0.5);
}
.dialog-drag.dialog-3.fixed {
  maring: 2em;
  outline: #1aad8d 2px dashed;
  outline-offset: 0.25em;
}
@-webkit-keyframes dialog-3-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1) scaleX(3);
            transform: scaleX(0.1) scaleX(3);
}
100% {
    opacity: 1;
}
}
@keyframes dialog-3-anim {
0% {
    opacity: 0;
    -webkit-transform: scaleX(0.1) scaleX(3);
            transform: scaleX(0.1) scaleX(3);
}
100% {
    opacity: 1;
}
}


